.sidecartWrapper {
    /* Sticking side cart to right */
    position: fixed;
    z-index: 3;
    top: 65px;
    right: 0;
    bottom: 0;
    /* Transparent shadow with dark depth fading off */
    -webkit-box-shadow: 0 0 2em rgba(0, 0, 0, .7);
            box-shadow: 0 0 2em rgba(0, 0, 0, .7);
    /* Cart background */
    background-color: #fff;
    /* Cart width */
    width: 35%;
}

.sidecartContainer {
    /* Container to align cart items vertically */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /* height 100% is important to make cart-items section scrollable */
    height: 100%;
}

.cartHeader {
    font-size: 1.5rem;
    padding: 1rem;
    border-bottom: 1px solid #ddd;
    /* To Show Cart on left and item count on right */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.cartItems {
    /* flex: auto; Absorbs any extra space along the main axis */
    -ms-flex: auto;
        flex: auto;
    /* Brings scroll bar */
    overflow-x: hidden;
    overflow-y: auto;
    scroll-behavior: smooth;
}

.cartItemWrapper {
    padding: 1rem;
    border-bottom: 1px solid #ddd;
}

.cartItem {
    display: -ms-flexbox;
    display: flex;
}

.cartItemImgWrapper {
    position: relative;
    margin-right: 1rem;
}

.cartItemQuantityPrice {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-top: 1rem;
}

